    background-color: #fff;
    border: 1px solid #e1e1e1;
    border-radius: 0.17rem;
    box-shadow: 0 0.08rem 0.25rem rgba(0, 0, 0, .1);
}
.favorite-person-intro {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
