    height: 2rem;
    margin-bottom: 0.25rem;
    border-radius: 1rem;
    box-shadow: 0 0.17rem 0.5rem rgba(0, 0, 0, .1);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow:hidden;
}
.favorite-person-cover img{
    width:100%;
}
