
.search_inner_box{
    width:5.9rem;
    margin:0 auto;
}
.search_inner{
   width:5.9rem;
   height:0.69rem; 
   margin-top:0.3rem;
   border:1px solid #eee;
   border-radius:4px;
}
.search_btn{
    width:1rem;
    height:0.69rem; 
    line-height:0.69rem; 
    cursor:pointer;
    border-left:1px solid #eee;
}
.search_i_default{
    width:1rem;
    height:0.69rem;
    line-height:0.69rem; 
    border-right:1px solid #eee;
}
.search_i_ipt{
    width:3.6rem;
    height:0.69rem;
}
.fl{
    float:left;
}
.fr{
    float:right;
}
.hot{
    margin-top:0.9rem;
}
.hot_title{
    padding-left: 0.4rem;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    font-size: 16px;
    font-weight: bold;
    color: #444;
    border-left: 0.1rem solid #47a86c;
    text-align:left;
}
.hot_search_box{
    margin-top:0.14rem;
}



