
.menu{
  -ms-flex:1;
      flex:1;
}
.movie_content{
  margin-bottom:0.88rem;
}
.movie_box{
  width:3rem;
  height:5.3rem;
  border:1px solid #e4e4e4;
  border-radius:4px;
  margin-left:0.12rem;
  margin-top:0.12rem;
  box-shadow: 0 10px 20px #eee;
  float:left;
  overflow:hidden;
}
.movie_images{
  height:4.2rem;
  width:3rem;
  overflow:hidden;
  position:relative;
}
.movie_images img{
  height:4.2rem;
  width:100%;
  z-index:2;
}
.movie_title{
  height:0.54rem;
  line-height:0.54rem;
  text-align:left;
  padding-left:0.14rem;
  color:#000;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.i_genner_box{
  padding-left:0.14rem;
}
.i_genner{
  float:left;
  padding-left:0.1rem;
  padding-right:0.1rem;
  height:0.4rem;
  border-radius:6px;
  box-shadow: 0 0 0 1px #ccc;
  text-align:center;
  line-height:0.4rem;
  margin-right:0.06rem;
  color: #666;
  margin-bottom:0.06rem;
  background-color: #fafafa;
}
.film-rating{
  position:absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.46rem; 
  line-height:0.46rem;
  background-color: rgba(0, 0, 0, .65);
  text-align: right;
  color:#fff;
  z-index:3;
  font-size: 12px;
}
.film-rating div{
  padding-right:0.2rem;
}
.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  height: 0;
}
.clearfix {
  zoom: 1;
}



